import * as React from 'react';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import './styles/RangeSlider.scss';

type Props = {
  handleOnSetWeight: (firstNum: number, secondNum: number) => void,
  weight: number[]
};

export const RangeSlider: React.FC<Props> = ({ handleOnSetWeight, weight, }) => {
  const [value, setValue] = React.useState<number[]>(weight);

  const handleChange = (event: Event, newValue: number | number[]) => {
    setValue(newValue as number[]);
  };

  React.useEffect(() => {
    handleOnSetWeight(value[0], value[1])
  }, [value])

  return (
    <div className='range__picker'>
      <div className='range__picker--text'>
        <div className='range__picker--text-text'>
          от
          <div
            onBlur={(e) => {
              console.log(+e.currentTarget.innerText)
            }}
            onKeyDown={(e) => {
              if (e.code.includes('Key')) {
                e.preventDefault();
                return;
              }

              if (e.currentTarget.innerText.length > 2) {
                if (e.code.includes('Backsp')) {
                  return;
                }
                e.preventDefault();
              }

            }}
            contentEditable
            className='range__picker-weight'
          >
            {weight[0]}
          </div>
          кг
        </div>

        <div className='range__picker--text-text'>
          до
          <div
            onBlur={(e) => {
              console.log(+e.currentTarget.innerText)
            }}
            onKeyDown={(e) => {
              if (e.code.includes('Key')) {
                e.preventDefault();
                return;
              }

              if (e.currentTarget.innerText.length > 2) {
                if (e.code.includes('Backsp')) {
                  return;
                }
                e.preventDefault();
              }

            }}
            contentEditable
            className='range__picker-weight'
          >
            {weight[1]}
          </div>
          кг
        </div>
      </div>
      <Box sx={{ width: '100%', margin: 0, zIndex: 1, }}>
        <Slider
          value={value}
          onChange={handleChange}
          valueLabelDisplay="auto"
          disableSwap
          sx={{
            'boxShadow': 'none',
            'color': '#5A8FFF',
            // 'color': 'red',
            '& .Mui-active: active': {
              all: 'unset',
            },
            '& css-eg0mwd-MuiSlider-thumb:hover': {
              all: 'unset',
            },
            '& .MuiSlider-track': {
              'border': 'none',
              'background': 'linear-gradient(180deg, #5A8FFF 100%, #003094 100%)'
            },
            '& .MuiSlider-thumb': {
              'width': 4,
              'height': 13,
              'borderRadius': 2,
              'background': 'linear-gradient(180deg, #5A8FFF 0%, #003094 100%)',
              '&::before': {
                'content': 'none'
              },
              '&::after': {
                'content': 'none'
              },
            },
          }}
        />
      </Box>
    </div>
  );
}
