import React, { useEffect, useRef, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';

import SimpleBarReact from "simplebar-react";
import "simplebar/src/simplebar.css";
import { ReactComponent as ArrowDown } from '../img/arrowDown.svg';
import { ReactComponent as ArrowUp } from '../img/arrowUp.svg';
import { ReactComponent as Actions } from '../img/actions.svg';
import { ReactComponent as Cross } from '../img/cross.svg';
import { ReactComponent as ActualIcon } from '../img/actualIcon.svg';

import classNames from 'classnames';

import { Data } from '../Types/Data';
import { ResizeableTable } from '../Resizeable';


type Props = {
  data: Data[],
  currentPage: number,
  onPageChange: (pageNumber: number, foundedItems?: Data[]) => void,
  total: Data[],
  isImageView: boolean,
  settingFields: boolean,
  handleOnSort: (type: string, fieldName: string) => void,
}

const tableHeaders = [
  "Наименования",
  "Артикул",
  "Категория",
  "Тип",
  "Ед.изм.",
  'Цена зак.',
  'Цена прод.',
  'Статус',
];

export const Table: React.FC<Props> = ({
  data,
  isImageView,
  settingFields,
  handleOnSort,
}) => {
  const [isCheckedHeader, setIsCheckedHeader] = useState<boolean>(false);
  const [headers, setHeaders] = useState<string[]>(tableHeaders);

  const swapElements = (array: string[], index1: number, index2: number) => {
    const temp = array[index1];
    array[index1] = array[index2];
    array[index2] = temp;

    const copy = [...array];
    setHeaders(copy);
  };

  useEffect(() => {
    // console.log(headers);
  }, [headers]);

  return (
    <>
      {settingFields ? (
        <ResizeableTable swapElements={swapElements} headers={headers} />
      )
        : (
          <table className='fields__table table'>
            <thead className='table__thead'>
              <tr>
                <th>
                  <label className="container">
                    <input
                      type="checkbox"
                      onChange={() => setIsCheckedHeader(!isCheckedHeader)}
                    />
                    <span className="checkmark"></span>
                  </label>
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                  Наименование <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('num', e.currentTarget.innerText)
                  }}
                >
                  Артикул <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                  Категория <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                  Тип <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                  Ед.изм. <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('num', e.currentTarget.innerText)
                  }}
                >
                  Цена зак. <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('num', e.currentTarget.innerText)
                  }}
                >
                  Цена прод. <ArrowUp />
                </th>
              </tr>
              <tr>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                  Статус <ArrowUp />
                </th>
              </tr>
              <tr>
                <th>
                  <Actions />
                </th>
              </tr>
            </thead>
            <SimpleBarReact style={{
              maxHeight: 695,
            }}
              forceVisible="y"
              autoHide={false}
            >
              <tbody className={classNames(
                'table__tbody',
                {
                  'table__flex-column': isImageView,
                }
              )}>
                {isImageView
                  ? (
                    data.map(item => (
                      <tr key={uuidv4()} className='photo__card card'>
                        <td className='photo__card--wrapper'>
                          <div className='card__title'>
                            <div className='card__checkbox'>
                              <label className="container">
                                {isCheckedHeader ?
                                  (<input defaultChecked={true} type="checkbox" />)
                                  : (<input defaultChecked={false} type="checkbox" />)}
                                <span className="checkmark"></span>
                              </label>
                            </div>
                            <h2 className='card__title-text'>{item.name}</h2>
                            <div className='card__title-actions'><Actions /></div>
                          </div>
                          <div className='card__under-title'>
                            <p className='card__serial-num'>{item.vendorCode}</p>
                            <p className='card__item-name'>{item.category}</p>
                            <p className='card__prices'>
                              <span>{item.perchasePrice}$</span>
                              <span className='card__price-second'>{item.salePrice}$</span>
                            </p>
                          </div>
                          <div className='card__img'>
                            <img src="https://cdn.shopify.com/s/files/1/0554/6747/5144/products/Boleslawiecmiska2.2_195x195@2x.png?v=1632312119" alt="" />
                          </div>
                          <div className="card__footer">
                            <div>
                              <span className={`table__item item-status item-status-${item.status}`}>
                                <span className={`item-status__wrapper-${item.status}`}>
                                  {item.status === 'red' ? (
                                    <>
                                      Удалённый
                                      <span className='item-status__icon'>
                                        <Cross />
                                      </span>
                                    </>
                                  ) : (
                                    <>
                                      Актуальный
                                      <span className='item-status__icon'>
                                        <ActualIcon />
                                      </span>
                                    </>
                                  )}
                                </span>
                              </span>
                            </div>
                            <div className="card__count">
                              <p>Товар</p>
                              <p>Шт.</p>
                            </div>
                          </div>
                        </td>
                      </tr>
                    ))
                  )
                  : (
                    <>
                      {data && (
                        data.map((item, index) => (
                          <tr key={uuidv4()}>
                            <td>
                              <label className="container">
                                {isCheckedHeader ?
                                  (<input defaultChecked={true} type="checkbox" />)
                                  : (<input defaultChecked={false} type="checkbox" />)}
                                <span className="checkmark"></span>
                              </label>
                            </td>
                            <td>{item.name}</td>
                            <td>{item.vendorCode}</td>
                            <td>{item.category}</td>
                            <td>{item.type}</td>
                            <td>{item.unitType}</td>
                            <td>{item.perchasePrice}$</td>
                            <td>{item.salePrice}$</td>
                            <td>
                              <span className={`table__item item-status item-status-${item.status}`}>
                                <span className={`item-status__wrapper-${item.status}`}>
                                  {item.status === 'red' ? (
                                    <>
                                      Удалённый
                                      <span className='item-status__icon'>
                                        <Cross />
                                      </span>
                                    </>
                                  ) : (
                                    <>
                                      Актуальный
                                      <span className='item-status__icon'>
                                        <ActualIcon />
                                      </span>
                                    </>
                                  )}
                                </span>
                              </span>
                            </td>
                            <td><Actions /></td>
                          </tr>
                        ))
                      )}
                    </>
                  )
                }

              </tbody>
            </SimpleBarReact>
          </table >
        )}

    </>
  );
}
